@import "../../scss/variables";
@import "../../scss/animations";

.root {
  margin: 0 calc($index * 4);
  margin-bottom: calc($index * 3.466667);

  &__slide {
    width: fit-content;
    animation: visbilityChangeIn 1s linear;

    &_title {
      color: $blue;
      font-family: $secondFontFamily;
      font-size: 25px;
      font-weight: 400;
      line-height: 120%; /* 30px */
      text-transform: uppercase;

      margin-bottom: 15px;

      @media (max-width: 768px) {
        font-size: 16px;
      }
    }
    &_text {
      max-width: 400px;
      height: 90px;

      color: $black-blue;
      font-size: 20px;
      font-weight: 400;
      line-height: 30px; /* 150% */

      @media (max-width: 768px) {
        max-width: 166px;
        height: 80px;

        font-size: 14px;
        line-height: 145%; /* 20.3px */
      }
    }
  }

  &_prevBtn,
  &_nextBtn {
    position: absolute;
    top: 41px;
    background-color: white;
    width: 40px;
    height: 40px;
    border-radius: 100%;

    box-shadow: 0px 0px 15px rgba(56, 119, 238, 0.1);
  }
  &_prevBtn {
    left: 40px;
  }
  &_nextBtn {
    right: 40px;
  }

  @media (max-width: 768px) {
    margin: 0;
    margin-bottom: calc($index * 15.878378);
  }
}
