@import "./scss/variables";
@import "./scss/mixins";

* {
  padding: 0px;
  margin: 0px;
  border: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Links */

a,
a:link,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

body {
  margin: 0;
  font-family: $mainFontFamily;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $bgcolor;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1,
h2 {
  font-family: $mainFontFamily;
}

h3,
h4,
h5,
h6 {
  font-family: $secondFontFamily;
}

.gradient {
  border: 5px solid;
  border-image: linear-gradient(to bottom, $blue 100%, $fuschia-100 100%);
  margin: 60px;
}

.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.swiper {
  position: static;
}

.swiper-button-prev:after,
.swiper-button-next:after {
  font-size: 12px;
}

.swiper-button-prev,
.swiper-button-next {
  color: $blue;

  width: 40px;
  height: 40px;
  border-radius: 100%;
  background-color: white;
  box-shadow: 0px 0px 15px rgba(56, 119, 238, 0.1);

  transition: 0.3s linear;
}

.swiper-button-prev.swiper-button-disabled,
.swiper-button-next.swiper-button-disabled {
  opacity: 0;
}

.swiper-button-next,
.swiper-rtl .swiper-button-prev {
  right: calc($index * 1.333333);
  top: auto;
  bottom: calc($index * 3.466667 + 45px);
}

.swiper-button-prev,
.swiper-rtl .swiper-button-next {
  left: calc($index * 1.333333);
  top: auto;
  bottom: calc($index * 3.466667 + 45px);
}

.swiper-slide {
  @media (max-width: 768px) {
    transition: 0.3s linear;
    opacity: 0.4;
  }
}

.swiper-slide-active {
  @media (max-width: 768px) {
    opacity: 1;
  }
}

.swiper-pagination {
  @include dFlexACenter();

  justify-content: center;

  gap: 10px;
}

.swiper-pagination-bullet {
  width: 6px;
  height: 6px;
  background-color: $black-blue;
}

.swiper-pagination-bullet-active {
  background-color: $black-blue;
}

.swiper-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet,
.swiper-pagination-horizontal.swiper-pagination-bullets
  .swiper-pagination-bullet {
  margin: 0;
}

.swiper-pagination-fraction,
.swiper-pagination-custom,
.swiper-horizontal > .swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 32px;
}
